.collection-item {
  // background-color: orange;
  min-width: 200px;
  margin: 0px 20px 0 20px;
  height: 300px;


  .image {
    width: 100%;
    height: 80%;

    &:hover {
      opacity: 0.5;
      transition: opacity 0.3s;
    }
  }

  .info {
    display: flex;
    height: 20%;
    // flex-flow: row;
    justify-content: space-between;

    p {
      margin-top: 2px;
    }
  }
}