.image {
  width: 100%;
  height: 80%;
  position: relative;
  overflow: hidden;

  &:hover {
    opacity: 0.5;
    transition: opacity 0.3s;
  }
}

.mouse-overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(180, 180, 180, 0.514);

  transition: all 0.5s;
}

.animateToCenter {
  animation: animate1 0.3s forwards ease-in-out;
}








@keyframes animate1 {
  0% {}

  100% {
    transform: translate(0, 0);
  }
}

@keyframes animateGoRight {
  0% {
    transform: translate(0, 0);
  }

  100% {
    transform: translate(100%, 0);
  }
}

@keyframes animateGoLeft {
  0% {
    transform: translate(0, 0);
  }

  100% {
    transform: translate(-100%, 0);
  }
}

@keyframes animateGoUp {
  0% {
    transform: translate(0, 0);
  }

  100% {
    transform: translate(0, -100%);
  }
}

@keyframes animateGoDown {
  0% {
    transform: translate(0, 0);
  }

  100% {
    transform: translate(0, 100%);
  }
}